:root {
  --focus-color: white;
  --second-color: rgba(245, 245, 245, 0.8);
}
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}


div.conteiner {
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
div.conteiner * {
  margin-top: 0;
}
div.conteiner h1.title {
  color: var(--focus-color);
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: 100px;
  margin: 0;
  margin-left: -5px;
}

div.conteiner p.description {
  color: var(--second-color);
  font-size: 1.1rem;
  margin: 10px 0;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

div.btn-begin {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  outline: solid white 1px;
  margin: 5px;
  margin-left: 60px;
}
div.btn-begin:hover {
  background-color: rgba(245, 245, 245, 0.074);
}
.dot {
  display: flex;
  align-items: center;
  flex-direction: column;
  transition: all;
  z-index: 100;
  pointer-events: all;
}

.dot img {
  border: solid rgb(119, 46, 255);
  border-radius: 50%;
  width: 35px;
  height: 35px;
}
.dot h3,
.dot h4 {
  color: white;
  margin: 0;
  text-align: center;
}

div.presentation {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: flex-start;
  padding: 0 150px;
  z-index: 100;
}

/*animations props*/
.text-animated-conteiner {
  overflow: hidden;
  /*  border:  solid red 1px;*/
  width: 550px;
}
.animated-item {
  position: relative;
  color: white;
}

/*dots are not visible by default*/
.dot-animated-conteiner{
  overflow: hidden;
 /* border:  solid red 1px;*/
  display: none;
}
.visible-dot{
  display:  flex !important;
}
.animated-item-dot{
  position: relative;
}

/*style each element*/
p.subtitle {
  color: var(--second-color);
  font-size: 1.4rem;
  margin: 0;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
div.title {
  height: auto;
  color: white;
  font-size: 100px;
}

p.description {
  margin: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande";
  color: var(--focus-color);
}
p.intro-footer {
  display: flex;
  width: fit-content;
  align-items: center;
  justify-content: space-between;
  margin: 0;
}


.dg.ac{
  z-index: 110 !important ;
}